}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch__icon {
  fill: currentColor;
  transition: fill .32s linear;
}

@media screen and (min-width: 768px) {
  .switch__icon {
    display: none;
  }
}

.switch__text {
  color: inherit;
  transition: color .32s linear;
  display: none;
}

@media screen and (min-width: 768px) {
  .switch__text {
    display: block;
  }
}

.light-theme .light-theme-box, .light-theme .dark-theme-box {
  color: #5f6775;
}

.dark-theme .dark-theme-box, .dark-theme .light-theme-box {
