}

.dark-theme-box.active, .light-theme-box.active {
  color: #4440f7;
}

.light-theme-box, .dark-theme-box {
  display: flex;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background: #4b48db center no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.switch__input:checked + .slider {
  background-color: #4440f7;
}

.switch__input:checked + .slider:before {
  background: #fff center no-repeat;
  transform: translateX(22px);
}

.slider.round {
  border: 1px solid #4b48db;
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

